import React from 'react'
const mappingData = [
    {
        label: "Department",
        value: "department",
        totalCount: 2,
        imgUrl: 'networking.png'
    },
    {
        label: "Functions",
        value: "functions",
        totalCount: 2,
        imgUrl: 'settings.png'
    },
    {
        label: "Projects",
        value: "projects",
        totalCount: 1,
        imgUrl: 'projectIcon.png'
    },
    {
        label: "Designation",
        value: "designation",
        totalCount: 1,
        imgUrl: 'designation.png'
    },

    {
        label: "Skills",
        value: "skills",
        totalCount: 1,
        imgUrl: 'skills.png'
    }


]
const MappingHeader = ({setIsActiveTab,isActiveTab}) => {

    const renderMappingHeaders = () => {
        return (mappingData?.map((item, ind) => <div className="col px-md-2 mb-3">
            <div className={`cardBox h-100 daysBox `} style={{
                minHeight: '80px',
                background: "#D8E8F7",
                ...(item?.value ==isActiveTab && { border: "1px solid #60848d" })
            }}
            onClick={()=>setIsActiveTab(item?.value)}
            >
                <figure>
                    <img width={30} src={`images/${item?.imgUrl}`} alt="" />
                </figure>
                <div>
                    <span className="d-block " style={{ fontSize: "16px" }}>{item?.label}</span>
                    <strong className="d-block " style={{ float: "inline-end" }}>{item?.totalCount}</strong>
                </div>

            </div>
        </div>
        ))
    }
    return (
        <div className="row px-md-1 mb-3">
            {renderMappingHeaders()}
        </div>
    )
}

export default MappingHeader
