import React, { useEffect, useMemo, useState } from 'react'
import { CustomButton } from '../../../globalComponents'
import moment from 'moment-timezone'
import CustomTable from '../../../reuseableComponents/customTable/CustomTable'
import { getBadgeColor } from '../../../../utils/common'
import { useCustomTranslation } from '../../../../translation'
import { Icon } from '../../../commanComponent'
const DepartmentTable = (props) => {
  const { t } = useCustomTranslation()
  const {
    departmentList,
    isLoading,
    setSelectedDepartmentData,
    toggleDepartmentModal,
    toggleDeleteModal,
    currentPage,
    setCurrentPage,
  } = props
  const [depList, setDepList] = useState(departmentList)


  const updateDepartment = (data) => {
    toggleDepartmentModal(data)
  }

  useEffect(() => {
    setDepList(departmentList)
  }, [departmentList])


  const scopedSlots = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    is_active: item => (
      <span
        className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
      >
        {item.is_active ? "Active" : "Inactive"}
      </span>
    ),
    created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
    action: item => (
      <div className="btn-group actionDrp">
        <button type="button" className="btn dropdown-toggle-split"
          data-bs-toggle="dropdown" aria-expanded="false">
          <i className="bi bi-three-dots-vertical"></i>
        </button>
        <ul className="dropdown-menu">
          <li>
            <span className="dropdown-item"
              onClick={() => {
                updateDepartment(item);
              }}
            >
              <Icon className={"bi bi-pencil-square me-1"} />Update</span>
          </li>
          {item?.emp_count === 0 && <li>
            <span className="dropdown-item"
              onClick={() => toggleDeleteModal(item._id)}
            >
              <Icon className={"bi bi-trash me-1"} />Delete</span>
          </li>}
        </ul>
      </div>

    )
  }), [setSelectedDepartmentData, toggleDepartmentModal, toggleDeleteModal]);


  const fields = [
    { key: 'sn', label: 'S.N' },
    { key: 'name', label: 'Name' },
    { key: 'short_name', label: 'Short Name' },
    { key: 'emp_count', label: 'Total Employees' },
    { key: 'is_active', label: 'Status' },
    { key: 'created_at', label: 'Created At' },
    { key: 'action', label: 'Action' }
  ];
  return (
    <>
      <CustomTable
        data={depList?.data}
        fields={fields}
        isLoading={isLoading}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={depList?.totalPages}
        tableName={"Department"}
        // onSearch={handleSearch}
        scopedSlots={scopedSlots}
      />


    </>

  )
}

export default DepartmentTable
