import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../../globalComponents'
import { createShortName } from '../../../../utils/common'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { CustCheckbox, Input, Label } from '../../../commanComponent'
import { validateSchema } from '../../../../utils/ValidationHelper'
import { addUpdateSkillSchema } from '../../../../utils/ValidationSchema'
import { useSelector } from 'react-redux'
import { useCustomTranslation } from '../../../../translation'

const CreateUpdateSkill = (props) => {
    const { createSkillPending, updateSkillPending } = useSelector(state => state?.department)
    const { t } = useCustomTranslation()
    const {
        selectedSkillData,
        openModel,
        handleOpenClose,
        handleCreateUpdateSkill
    } = props
    const [errors, setErrors] = useState({})
    const [createUpdateSkillFormData, setCreateUpdateSkillFormData] = useState({
        name: '',
        short_name: '',
        is_active: true
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e.target ? e.target : { value: e };
        setCreateUpdateSkillFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === 'name') {
                updatedState.short_name = createShortName(value);
            }
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateSkill = async () => {
        const validationResponse = await validateSchema(addUpdateSkillSchema, createUpdateSkillFormData);
        if (validationResponse.isValid) {
            const {
                name,
                short_name,
                is_active,
            } = createUpdateSkillFormData
            const params = {
                name,
                short_name,
                is_active,
            }
            setErrors({})
            handleCreateUpdateSkill(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedSkillData) {
            setCreateUpdateSkillFormData({
                name: selectedSkillData?.name,
                short_name: selectedSkillData?.short_name,
                is_active: selectedSkillData?.is_active
            })
        }
    }, [selectedSkillData])

    const resetForm = () => {
        setCreateUpdateSkillFormData({
            name: '',
            short_name: '',
            is_active: true
        })
        setErrors({})
    }

    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                        {selectedSkillData ? t("headings.updateSkill") : t("headings.createSkill")}
                    </h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>


                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">


                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.skillName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.skillName")}
                                        value={createUpdateSkillFormData?.name}
                                        name="name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.name && (
                                        <CustomError text={errors.name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.shortName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.shortName")}
                                        value={createUpdateSkillFormData?.short_name}
                                        name="short_name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.short_name && (
                                        <CustomError text={errors.short_name} />
                                    )}
                                </div>
                            </div>

                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        // label={"Active"}
                                        checked={createUpdateSkillFormData?.is_active}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_active"}
                                    />

                                </div>
                            </div>
                        </div>

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedSkillData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdateSkill()}
                            isLoading={selectedSkillData ? updateSkillPending : createSkillPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateSkill
