import React, { useEffect, useMemo, useState } from 'react'
import { CustomButton } from '../../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import {  departmentAction } from '../../../../core/resource/actions'
import { pagination } from '../../../../utils/Constants.config'
import ApproveRejectModal from '../../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../../translation'
import CreateUpdateFunction from './CreateUpdateFunction'
import { CustomTable } from '../../../reuseableComponents'
import { Icon } from '../../../commanComponent'
import { dateFormate, getBadgeColor } from '../../../../utils/common'

const Functions = (props) => {
    const {allDepartments,allEmployeeList} =props
    const dispatch = useDispatch()
    const { t } = useCustomTranslation();

    const {
        createSubDepartmentPending,
        createSubDepartmentRes,
        createSubDepartmentError,

        getSubDepartmentPending,
        getSubDepartmentRes,
        getSubDepartmentError,

        updateSubDepartmentPending,
        updateSubDepartmentRes,
        updateSubDepartmentError,

        deleteFunctionPending,
        deleteFunctionRes,
        deleteFunctionError,
    } = useSelector(state => state.department)

    const [isOpenCreateUpdateFunction, setIsOpenCreateUpdateFunction] = useState(false)
    const [selectedFunctionData, setSelectedFunctionData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [allFunctions, setAllFunctions] = useState({})
    const [isShowDeleteFunctionModal, setIsShowDeleteFunctionModal] = useState(false)
    const [deleteFunctionId, setDeleteFunctionId] = useState('')


    /* Create Update Function */
    const toggleCreateUpdateFunctionModal = (data) => {
        if (!data) {
            setSelectedFunctionData('')
        } else {
            setSelectedFunctionData(data)
        }
        setIsOpenCreateUpdateFunction(!isOpenCreateUpdateFunction)
    }

    const handleCreateUpdateFunction = (params) => {
        if (selectedFunctionData) {
            params['_id'] = selectedFunctionData?._id || selectedFunctionData?.id
            dispatch(departmentAction.updateSubDepartmentRequest(params));
        }
        else {
            dispatch(departmentAction.createSubDepartmentRequest(params));
        }
    }

    useEffect(() => {
        if (!createSubDepartmentPending) {
            if (createSubDepartmentError) {
                dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_CREATE_SUB_DEPARTMENT_STORE"))
                return;
            }
            if (createSubDepartmentRes) {
                if (createSubDepartmentRes?.code === 200) {
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_CREATE_SUB_DEPARTMENT_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_SUB_DEPARTMENT_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_ALL_SUB_DEPARTMENT_LIST_STORE"))
                    getAllFunctions()
                    toggleCreateUpdateFunctionModal()
                }
            }
        }
    }, [
        createSubDepartmentRes,
        createSubDepartmentError,
        createSubDepartmentPending
    ]);

    useEffect(() => {
        if (!updateSubDepartmentPending) {
            if (updateSubDepartmentError) {
                dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_UPDATE_SUB_DEPARTMENT_STORE"))
                return;
            }
            if (updateSubDepartmentRes) {
                if (updateSubDepartmentRes?.code === 200) {
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_UPDATE_SUB_DEPARTMENT_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_SUB_DEPARTMENT_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_ALL_SUB_DEPARTMENT_LIST_STORE"))
                    getAllFunctions()
                    toggleCreateUpdateFunctionModal()
                }
            }
        }
    }, [
        updateSubDepartmentRes,
        updateSubDepartmentError,
        updateSubDepartmentPending
    ]);

   
    useEffect(() => {
        getAllFunctions()
    }, [currentPage])

    /* Get All Functions Data */
    const getAllFunctions = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(departmentAction.getSubDepartmentRequest(params));
    }

    useEffect(() => {
        if (!getSubDepartmentPending) {
            if (getSubDepartmentError) {
                return;
            }
            if (getSubDepartmentRes) {
                if (getSubDepartmentRes?.code === 200) {
                    const getAllFunctionData = getSubDepartmentRes?.data
                    setAllFunctions(getAllFunctionData)

                }
            }
        }
    }, [
        getSubDepartmentRes,
        getSubDepartmentError,
        getSubDepartmentPending
    ]);


    /*  Delete Function   */
    const handleFunctionDeleteModal = (id) => {
        if (id) {
            setDeleteFunctionId(id)
        }
        setIsShowDeleteFunctionModal(!isShowDeleteFunctionModal)
    }

    const handleDeleteFunction = (type) => {
        if (type) {
            const params = {
                _id: deleteFunctionId
            }

            // dispatch(departmentAction?.deleteFunctionRequest(params))

        } else {
            handleFunctionDeleteModal()
        }

    }

    useEffect(() => {
        if (!deleteFunctionPending) {
            if (deleteFunctionError) {
                // dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_DELETE_FUNCTION_STORE"))
                return;
            }
            if (deleteFunctionRes) {
                if (deleteFunctionRes?.code === 200) {
                    handleFunctionDeleteModal()
                    if (currentPage > 1 && allFunctions?.data?.length === 1) {
                        setCurrentPage((pre => pre -= 1))
                    } else {
                        getAllFunctions()
                    }
                    // dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_DELETE_FUNCTION_STORE"))

                }
            }
        }
    }, [
        deleteFunctionRes,
        deleteFunctionError,
        deleteFunctionPending
    ]);

    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        created_at: item => dateFormate(item.created_at),
        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                            onClick={() => toggleCreateUpdateFunctionModal(item)}
                        >
                            <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                    </li>
                    {/* {item?.emp_count === 0 && <li>
                        <span className="dropdown-item"
                            onClick={() => handleFunctionDeleteModal(item._id)}
                        >
                            <Icon className={"bi bi-trash me-1"} />Delete</span>
                    </li>} */}
                </ul>
            </div>

        )
    }), [toggleCreateUpdateFunctionModal]);


    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'department_name', label: 'Departments' },
        { key: 'name', label: 'Function Name' },
        { key: 'short_name', label: 'Short Name' },
        { key: 'emp_count', label: 'Total Employees' },
        { key: 'is_active', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];

    return (
        <>
            <div className='cardBox myRequest mb-3 employeeHistory'>
                <div className="d-flex align-items-center justify-content-between gap-3">
                    
                    <div className="cardHead d-flex">
                        <img src="images/myrequest-icon.svg" alt="" width={35} />
                        <h4 className="ms-2 ">Functions</h4>
                    </div>
                    <div>
                        <CustomButton
                            classNames={"btn btn-primary gradientBg"}
                            handleClick={() => toggleCreateUpdateFunctionModal()}
                            text={t("btns.createNewFunction")}
                            icon={<Icon className={"fa fa-plus"}/>}
                        />

                    </div>
                </div>
                <CustomTable
                    data={allFunctions?.data}
                    fields={fields}
                    isLoading={getSubDepartmentPending}
                    pagination={true}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    totalPages={allFunctions?.totalPages}
                    isHeader={false}
                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={scopedSlots}
                />
            </div>

            <CreateUpdateFunction
                openModel={isOpenCreateUpdateFunction}
                handleOpenClose={toggleCreateUpdateFunctionModal}
                handleCreateUpdateFunction={handleCreateUpdateFunction}
                selectedFunctionData={selectedFunctionData}
                allDepartments={allDepartments}
                allEmployeeList={allEmployeeList}
            />
            {isShowDeleteFunctionModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteFunction}
                    isShow={isShowDeleteFunctionModal}
                    desc={t("alerts.deletePopup.desc").replace('$1', "Function")}
                    btnRightLoading={deleteFunctionPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}

                />)
            }
        </>
    )
}

export default Functions
