import React, { useState, useEffect, useRef } from 'react';
import { useSelector } from 'react-redux'
import { CustomButton } from '../../../globalComponents';
import { addDepartmentSchema } from '../../../../utils/ValidationSchema';
import { validateSchema } from '../../../../utils/ValidationHelper';
import CustomError from '../../../globalComponents/CustomError';
import { CustCheckbox, Input, Label } from '../../../commanComponent';
import { useCustomTranslation } from '../../../../translation';

const CreateUpdateDepartment = (props) => {
    const { t } = useCustomTranslation()
    const {
        openCreateUpdateDepModal,
        toggleDepartmentModal,
        selectedDepartmentData,
        handleCreateUpdateDepartment
    } = props
    const [errors, setErrors] = useState({});
    const {
        addDepartmentPending,
        updateDepartmentPending,
    } = useSelector(state => state.department);
    const [isActive, setIsActive] = useState(true);
    const [depData, setDepData] = useState({
        depName: '',
        shortName: '',
    })

    const submitCreateUpdateDepartment = async () => {
        const validationResponse = await validateSchema(addDepartmentSchema, depData);
        if (validationResponse.isValid) {
            setErrors({});
            const params = {
                name: depData?.depName || '',
                short_name: depData?.shortName || '',
                is_active: isActive ? true : false
            }
            handleCreateUpdateDepartment(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    const handleChange = (event) => {
        errors && setErrors({});
        const { name, value } = event.target
        setDepData({ ...depData, [name]: value })
    }

    // Update Department 
    useEffect(() => {
        if (selectedDepartmentData) {
            setDepData({
                depName: selectedDepartmentData?.name || '',
                shortName: selectedDepartmentData?.short_name || '',
            })
            setIsActive(selectedDepartmentData?.is_active)
        }
    }, [selectedDepartmentData])


    useEffect(() => {
        return (() => {
            resetDepartmentData()
        })
    }, [openCreateUpdateDepModal])


    const resetDepartmentData = () => {
        setDepData({
            depName: '',
            shortName: '',
        })
        setErrors({});
    }

    return (
        <>
            <div className={`offcanvas offcanvas-end creatNewTicketOff ${openCreateUpdateDepModal ? 'show' : ''}`} tabIndex={-1}
                style={{ visibility: openCreateUpdateDepModal ? 'visible' : 'hidden' ,
                      width: "800px"
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary"> {selectedDepartmentData ? "Update" : "Create New"} Department</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => toggleDepartmentModal()}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.name")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.departmentName")}
                                        value={depData?.depName || ''}
                                        name="depName"
                                        onChange={(e) => handleChange(e)}
                                    />
                                    {errors?.depName && (
                                        <CustomError text={errors.depName} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group">
                                    <Label text={t("labels.shortName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.shortName")}
                                        value={depData?.shortName}
                                        name="shortName"
                                        onChange={(e) => handleChange(e)}
                                    />

                                    {errors?.shortName && (
                                        <CustomError text={errors.shortName} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        // label={"Active"}
                                        checked={isActive}
                                        handleChange={(e) => setIsActive(!isActive)}
                                        name={"is_active"}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>


                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedDepartmentData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdateDepartment()}
                            isLoading={selectedDepartmentData ? updateDepartmentPending : addDepartmentPending}
                        />
                    </div>
                </div>
            </div>
            {openCreateUpdateDepModal && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateDepartment
