import React, { useEffect, useMemo, useState } from 'react'
import { CustomButton } from '../../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import {  departmentAction } from '../../../../core/resource/actions'
import { pagination } from '../../../../utils/Constants.config'
import ApproveRejectModal from '../../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../../translation'
import CreateUpdateSkill from './CreateUpdateSkill'
import { CustomTable } from '../../../reuseableComponents'
import { Icon } from '../../../commanComponent'
import { dateFormate, getBadgeColor } from '../../../../utils/common'

const Skills = (props) => {
    const {allDepartments} =props
    const dispatch = useDispatch()
    const { t } = useCustomTranslation();

    const {
        createSkillPending,
        createSkillRes,
        createSkillError,

        getSkillPending,
        getSkillRes,
        getSkillError,

        updateSkillPending,
        updateSkillRes,
        updateSkillError,

        deleteSkillPending,
        deleteSkillRes,
        deleteSkillError,
    } = useSelector(state => state.department)

    const [isOpenCreateUpdateSkill, setIsOpenCreateUpdateSkill] = useState(false)
    const [selectedSkillData, setSelectedSkillData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [allSkills, setAllSkills] = useState({})
    const [isShowDeleteSkillModal, setIsShowDeleteSkillModal] = useState(false)
    const [deleteSkillId, setDeleteSkillId] = useState('')


    /* Create Update Skill */
    const toggleCreateUpdateSkillModal = (data) => {
        if (!data) {
            setSelectedSkillData('')
        } else {
            setSelectedSkillData(data)
        }
        setIsOpenCreateUpdateSkill(!isOpenCreateUpdateSkill)
    }

    const handleCreateUpdateSkill = (params) => {
        if (selectedSkillData) {
            params['_id'] = selectedSkillData?._id || selectedSkillData?.id
            dispatch(departmentAction.updateSkillRequest(params));
        }
        else {
            dispatch(departmentAction.createSkillRequest(params));
        }
    }

    useEffect(() => {
        if (!createSkillPending) {
            if (createSkillError) {
                dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_CREATE_SKILL_STORE"))
                return;
            }
            if (createSkillRes) {
                if (createSkillRes?.code === 200) {
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_CREATE_SKILL_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_SKILL_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_ALL_SKILL_LIST_STORE"))
                    toggleCreateUpdateSkillModal()
                    getAllSkills()
                }
            }
        }
    }, [
        createSkillPending,
        createSkillRes,
        createSkillError,
    ]);

    useEffect(() => {
        if (!updateSkillPending) {
            if (updateSkillError) {
                dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_UPDATE_SKILL_STORE"))
                return;
            }
            if (updateSkillRes) {
                if (updateSkillRes?.code === 200) {
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_UPDATE_SKILL_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_SKILL_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_ALL_SKILL_LIST_STORE"))
                    getAllSkills()
                    toggleCreateUpdateSkillModal()
                }
            }
        }
    }, [
        updateSkillRes,
        updateSkillError,
        updateSkillPending
    ]);

   
    useEffect(() => {
        getAllSkills()
    }, [currentPage])

    /* Get All Skills Data */
    const getAllSkills = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(departmentAction.getSkillRequest(params));
    }

    useEffect(() => {
        if (!getSkillPending) {
            if (getSkillError) {
                return;
            }
            if (getSkillRes) {
                if (getSkillRes?.code === 200) {
                    const getSkillData = getSkillRes?.data
                    setAllSkills(getSkillData)
                }
            }
        }
    }, [
        getSkillPending,
        getSkillRes,
        getSkillError,
    ]);


    /*  Delete Skill   */
    const handleSkillDeleteModal = (id) => {
        if (id) {
            setDeleteSkillId(id)
        }
        setIsShowDeleteSkillModal(!isShowDeleteSkillModal)
    }

    const handleDeleteSkill = (type) => {
        if (type) {
            const params = {
                _id: deleteSkillId
            }
            // dispatch(departmentAction?.deleteSkillRequest(params))

        } else {
            handleSkillDeleteModal()
        }

    }

    useEffect(() => {
        if (!deleteSkillPending) {
            if (deleteSkillError) {
                // dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_DELETE_FUNCTION_STORE"))
                return;
            }
            if (deleteSkillRes) {
                if (deleteSkillRes?.code === 200) {
                    handleSkillDeleteModal()
                    // if (currentPage > 1 && allSkills?.data?.length === 1) {
                    //     setCurrentPage((pre => pre -= 1))
                    // } else {
                    //     // getAllSkills()
                    // }
                    // dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_DELETE_FUNCTION_STORE"))

                }
            }
        }
    }, [
        deleteSkillRes,
        deleteSkillError,
        deleteSkillPending
    ]);

    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        created_at: item => dateFormate(item.created_at),
        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                            onClick={() => toggleCreateUpdateSkillModal(item)}
                        >
                            <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                    </li>
                    {/* {item?.emp_count === 0 && <li>
                        <span className="dropdown-item"
                            onClick={() => handleSkillDeleteModal(item._id)}
                        >
                            <Icon className={"bi bi-trash me-1"} />Delete</span>
                    </li>} */}
                </ul>
            </div>

        )
    }), [toggleCreateUpdateSkillModal]);


    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'name', label: 'Skill Name' },
        { key: 'short_name', label: 'Short Name' },
        { key: 'is_active', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];

    return (
        <>
            <div className='cardBox myRequest mb-3 employeeHistory'>
                <div className="d-flex align-items-center justify-content-between gap-3">
                    <div className="cardHead d-flex">
                        <img src="images/myrequest-icon.svg" alt="" width={35} />
                        <h4 className="ms-2 ">Skills</h4>
                    </div>
                    <div>
                        <CustomButton
                            classNames={"btn btn-primary gradientBg"}
                            handleClick={() => toggleCreateUpdateSkillModal()}
                            text={t("btns.createNewSkill")}
                            icon={<Icon className={"fa fa-plus"}/>}
                        />

                    </div>
                </div>
                <CustomTable
                    data={allSkills?.data}
                    fields={fields}
                    isLoading={getSkillPending}
                    pagination={true}
                    currentPage={currentPage}
                    setCurrentPage={(no)=>setCurrentPage(no)}
                    totalPages={allSkills?.totalPages}
                    isHeader={false}
                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={scopedSlots}
                />
            </div>

           { isOpenCreateUpdateSkill &&<CreateUpdateSkill
                openModel={isOpenCreateUpdateSkill}
                handleOpenClose={toggleCreateUpdateSkillModal}
                handleCreateUpdateSkill={handleCreateUpdateSkill}
                selectedSkillData={selectedSkillData}
                allDepartments={allDepartments}
            />}
            {isShowDeleteSkillModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteSkill}
                    isShow={isShowDeleteSkillModal}
                    desc={t("alerts.deletePopup.desc").replace('$1', "Skill")}
                    btnRightLoading={deleteSkillPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}

                />)
            }
        </>
    )
}

export default Skills
