import React, { useEffect, useState } from 'react'
import MappingHeader from './MappingHeader'
import Functions from './functions/Functions'
import { useDispatch, useSelector } from 'react-redux'
import { departmentAction, employeeActions } from '../../../core/resource/actions'
import Projects from './projects/Projects'
import Skills from './skills/Skills'
import { Department } from './department'
import Designation from './designation/Designation'

const Mapping = () => {
    const dispatch = useDispatch()
    const [isActiveTab, setIsActiveTab] = useState('department')
    const [allDepartments, setAllDepartments] = useState([])
    const [subDepartmentByDepartmentData,setSubDepartmentByDepartmentData]=useState([])
    const [allEmployeeList, setAllEmployeeList] = useState([])
    const {
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending,

        getSubDepartmentByDepartmentRes,
        getSubDepartmentByDepartmentError,
        getSubDepartmentByDepartmentPending,

    } = useSelector(state => state.department)

    const {
        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending

    } = useSelector(state => state.employee)



    useEffect(() => {
        getAllDepartments()
        getAllEmployee()
    }, [])

    /* Get All Departments */
    const getAllDepartments = () => {
        const departmentResposnse = getDepartmentListRes?.data
        if (!departmentResposnse?.length) {
            dispatch(departmentAction.getDepartmentListRequest());
        } else {
            setAllDepartments(departmentResposnse)
        }
    }

    useEffect(() => {
        if (!getDepartmentListPending) {
            if (getDepartmentListError) {
                return;
            }
            if (getDepartmentListRes) {
                if (getDepartmentListRes?.code === 200) {
                    const departmentResponse = getDepartmentListRes?.data
                    setAllDepartments(departmentResponse)
                }
            }
        }
    }, [
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    ]);

    /* Get All Employee List*/
    const getAllEmployee = () => {
        dispatch(employeeActions.getAllEmployeeRequest())
    }
    useEffect(() => {
        if (!getAllEmployeePending) {
            if (getAllEmployeeError) {
                return;
            }
            if (getAllEmployeeRes) {
                if (getAllEmployeeRes?.code === 200) {

                    setAllEmployeeList(getAllEmployeeRes?.data)
                }
            }
        }
    }, [
        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending
    ]);



    /* Get All Employee List*/
    const getSubDepartmentByDepartment = (id) => {
        const payload ={
            department_id:id
        }
        dispatch(departmentAction.getSubDepartmentByDepartmentRequest(payload))
    }
    useEffect(() => {
        if (!getSubDepartmentByDepartmentPending) {
            if (getSubDepartmentByDepartmentError) {
                return;
            }
            if (getSubDepartmentByDepartmentRes) {
                if (getSubDepartmentByDepartmentRes?.code === 200) {

                    setSubDepartmentByDepartmentData(getSubDepartmentByDepartmentRes?.data)
                }
            }
        }
    }, [
        getSubDepartmentByDepartmentRes,
        getSubDepartmentByDepartmentError,
        getSubDepartmentByDepartmentPending
    ]);


    return (
        <>
            <MappingHeader
                isActiveTab={isActiveTab}
                setIsActiveTab={setIsActiveTab}
            />
            {isActiveTab === "department" && <Department

            />}
            {isActiveTab === "functions" && <Functions
                allDepartments={allDepartments}
                allEmployeeList={allEmployeeList}
            />}
            {isActiveTab === "projects" && <Projects
                allDepartments={allDepartments}
                allEmployeeList={allEmployeeList}
                getSubDepartmentByDepartment={getSubDepartmentByDepartment}
                subDepartmentByDepartmentData={subDepartmentByDepartmentData}
            />}
            {isActiveTab === "designation" && <Designation
                allDepartments={allDepartments}
            />}
            {isActiveTab === "skills" && <Skills

            />}

        </>
    )
}

export default Mapping
