import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../../globalComponents'
import { createShortName } from '../../../../utils/common'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { CustCheckbox, Input, Label } from '../../../commanComponent'
import { validateSchema } from '../../../../utils/ValidationHelper'
import { addUpdateFunctionSchema } from '../../../../utils/ValidationSchema'
import { useSelector } from 'react-redux'
import { useCustomTranslation } from '../../../../translation'

const CreateUpdateFunction = (props) => {
    const { addFunctionPending, updateFunctionPending } = useSelector(state => state?.position)
    const { t } = useCustomTranslation()
    const {
        selectedFunctionData,
        openModel,
        handleOpenClose,
        allDepartments,
        allEmployeeList,
        handleCreateUpdateFunction
    } = props
    const [errors, setErrors] = useState({})
    const [createUpdateFunctionFormData, setCreateUpdateFunctionFormData] = useState({
        department: null,
        functional_head: null,
        name: '',
        short_name: '',
        is_active: true
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e.target ? e.target : { value: e };
        setCreateUpdateFunctionFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === 'name') {
                updatedState.short_name = createShortName(value);
            }
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateFunction = async () => {
        const validationResponse = await validateSchema(addUpdateFunctionSchema, createUpdateFunctionFormData);
        if (validationResponse.isValid) {
            const {
                department,
                name,
                short_name,
                is_active,
                functional_head,
            } = createUpdateFunctionFormData
            const params = {
                name,
                short_name,
                is_active,
                ["department_id"]: department?.value,
                ["function_head_id"]: functional_head?.value,
            }
            setErrors({})

            handleCreateUpdateFunction(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedFunctionData) {
            setCreateUpdateFunctionFormData({
                department: { value: selectedFunctionData?.department_id, label: selectedFunctionData?.department_name },
                functional_head: { value: selectedFunctionData?.function_head_id, label: selectedFunctionData?.function_head_name },
                name: selectedFunctionData?.name,
                short_name: selectedFunctionData?.short_name,
                is_active: selectedFunctionData?.is_active
            })
        }
    }, [selectedFunctionData])

    const resetForm = () => {
        setCreateUpdateFunctionFormData({
            department: null,
            name: '',
            short_name: '',
            functional_head: null,
            is_active: true
        })
        setErrors({})
    }

    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                        {selectedFunctionData ? t("headings.updateFunction") : t("headings.createFunction")}
                    </h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>


                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.selectDepartment")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allDepartments}
                                        placeholder={t("placeholders.selectDepartment")}
                                        value={createUpdateFunctionFormData?.department || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "department")}
                                        name="department"
                                    />
                                    {errors?.department && (
                                        <CustomError text={errors.department} />
                                    )}
                                </div>
                            </div>
                           
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.functionName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.functionName")}
                                        value={createUpdateFunctionFormData?.name}
                                        name="name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.name && (
                                        <CustomError text={errors.name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.shortName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.shortName")}
                                        value={createUpdateFunctionFormData?.short_name}
                                        name="short_name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.short_name && (
                                        <CustomError text={errors.short_name} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.selectFunctionalHead")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allEmployeeList}
                                        placeholder={t("placeholders.selectDesignation")}
                                        value={createUpdateFunctionFormData?.functional_head || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "functional_head")}
                                        name="functional_head"
                                    />
                                    {errors?.functional_head && (
                                        <CustomError text={errors.functional_head} />
                                    )}
                                </div>
                            </div>
                        </div>
                        
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        // label={"Active"}
                                        checked={createUpdateFunctionFormData?.is_active}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_active"}
                                    />

                                </div>
                            </div>
                        </div>

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedFunctionData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdateFunction()}
                            isLoading={selectedFunctionData ? updateFunctionPending : addFunctionPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateFunction
