import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../../globalComponents'
import { createShortName } from '../../../../utils/common'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { CustCheckbox, Input, Label } from '../../../commanComponent'
import { validateSchema } from '../../../../utils/ValidationHelper'
import { addUpdateProjectSchema } from '../../../../utils/ValidationSchema'
import { useSelector } from 'react-redux'
import { useCustomTranslation } from '../../../../translation'

const CreateUpdateProject = (props) => {
    const { createProjectPending, updateProjectPending } = useSelector(state => state?.department)
    const { t } = useCustomTranslation()
    const {
        selectedProjectData,
        openModel,
        handleOpenClose,
        allDepartments,
        allEmployeeList,
        getSubDepartmentByDepartment,
        subDepartmentByDepartmentData,
        handleCreateUpdateProject
    } = props
    const [errors, setErrors] = useState({})
    const [createUpdateProjectFormData, setCreateUpdateProjectFormData] = useState({
        department: null,
        sub_department:null,
        project_manager:null,
        function_head: null,
        name: '',
        short_name: '',
        is_active: true
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e.target ? e.target : { value: e };
        if (name === 'department') {
            getSubDepartmentByDepartment(value?.value);
        }
        setCreateUpdateProjectFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === 'name') {
                updatedState.short_name = createShortName(value);
            }
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateProject = async () => {
        const validationResponse = await validateSchema(addUpdateProjectSchema, createUpdateProjectFormData);
        if (validationResponse.isValid) {
            const {
                department,
                name,
                short_name,
                is_active,
                sub_department,
                project_manager,
                function_head,
            } = createUpdateProjectFormData
            const params = {
                name,
                short_name,
                ["department_id"]: department?.value,
                ["sub_department_id"]: sub_department?.value,
                project_manager_id:project_manager?.value,
                function_head_id:function_head?.value,
                is_active,
            }
            setErrors({})
            handleCreateUpdateProject(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedProjectData) {
            setCreateUpdateProjectFormData({
                department: { value: selectedProjectData?.department_id, label: selectedProjectData?.department_name },
                function_head: { value: selectedProjectData?.function_head_id, label: selectedProjectData?.function_head_name },
                name: selectedProjectData?.name,
                short_name: selectedProjectData?.short_name,
                is_active: selectedProjectData?.is_active,
                sub_department:{ value: selectedProjectData?.function_id, label: selectedProjectData?.function_name },
                project_manager: { value: selectedProjectData?.project_manager_id, label: selectedProjectData?.project_manager_name },
            })
        }
    }, [selectedProjectData])

    const resetForm = () => {
        setCreateUpdateProjectFormData({
            department: null,
            sub_department:null,
            project_manager:null,
            function_head: null,
            name: '',
            short_name: '',
            is_active: true
        })
        setErrors({})
    }

    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                        {selectedProjectData ? t("headings.updateProject") : t("headings.createProject")}
                    </h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>


                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">

                            <div className="col-md-6">
                                <Label text={t("labels.selectDepartment")} required={true} />
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allDepartments}
                                        placeholder={t("placeholders.selectDepartment")}
                                        value={createUpdateProjectFormData?.department || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "department")}
                                        name="department"
                                    />
                                    {errors?.department && (
                                        <CustomError text={errors.department} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label text={t("labels.selectFunction")} required={true} />
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={subDepartmentByDepartmentData}
                                        placeholder={t("placeholders.selectFunction")}
                                        value={createUpdateProjectFormData?.sub_department || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "sub_department")}
                                        name="sub_department"
                                    />
                                    {errors?.sub_department && (
                                        <CustomError text={errors.sub_department} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label text={t("labels.projectName")} required={true} />
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.projectName")}
                                        value={createUpdateProjectFormData?.name}
                                        name="name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.name && (
                                        <CustomError text={errors.name} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label text={t("labels.shortName")} required={true} />
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.shortName")}
                                        value={createUpdateProjectFormData?.short_name}
                                        name="short_name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.short_name && (
                                        <CustomError text={errors.short_name} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label text={t("labels.projectManager")} required={true} />
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allEmployeeList}
                                        placeholder={t("placeholders.projectManager")}
                                        value={createUpdateProjectFormData?.project_manager || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "project_manager")}
                                        name="project_manager"
                                    />
                                    {errors?.project_manager && (
                                        <CustomError text={errors.project_manager} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label text={t("labels.functionalManager")} required={true} />
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allEmployeeList}
                                        placeholder={t("placeholders.functionalManager")}
                                        value={createUpdateProjectFormData?.function_head || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "function_head")}
                                        name="function_head"
                                    />
                                    {errors?.function_head && (
                                        <CustomError text={errors.function_head} />
                                    )}
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        // label={"Active"}
                                        checked={createUpdateProjectFormData?.is_active}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_active"}
                                    />

                                </div>
                            </div>
                        </div>

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedProjectData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdateProject()}
                            isLoading={selectedProjectData ? updateProjectPending : createProjectPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateProject
