import React, { useEffect, useMemo, useState } from 'react'
import { CustomButton } from '../../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { departmentAction } from '../../../../core/resource/actions'
import { pagination } from '../../../../utils/Constants.config'
import ApproveRejectModal from '../../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../../translation'
import CreateUpdateProject from './CreateUpdateProject'
import { CustomTable } from '../../../reuseableComponents'
import { Icon } from '../../../commanComponent'
import { dateFormate, getBadgeColor } from '../../../../utils/common'

const Projects = (props) => {
    const { allDepartments,allEmployeeList,getSubDepartmentByDepartment ,subDepartmentByDepartmentData} = props
    const dispatch = useDispatch()
    const { t } = useCustomTranslation();

    const {
        createProjectPending,
        createProjectRes,
        createProjectError,

        addProjectPending,
        getProjectRes,
        getProjectError,

        updateProjectPending,
        updateProjectRes,
        updateProjectError,

        deleteProjectPending,
        deleteProjectRes,
        deleteProjectError,
    } = useSelector(state => state.department)

    const [isOpenCreateUpdateProject, setIsOpenCreateUpdateProject] = useState(false)
    const [selectedProjectData, setSelectedProjectData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [allProjects, setAllProjects] = useState({})
    const [isShowDeleteProjectModal, setIsShowDeleteProjectModal] = useState(false)
    const [deleteProjectId, setDeleteProjectId] = useState('')


    /* Create Update Project */
    const toggleCreateUpdateProjectModal = (data) => {
        if (!data) {
            setSelectedProjectData('')
        } else {
            setSelectedProjectData(data)
        }
        setIsOpenCreateUpdateProject(!isOpenCreateUpdateProject)
    }

    const handleCreateUpdateProject = (params) => {
        if (selectedProjectData) {
            params['_id'] = selectedProjectData?._id || selectedProjectData?.id
            dispatch(departmentAction.updateProjectRequest(params));
        }
        else {
            dispatch(departmentAction.createProjectRequest(params));
        }
    }

    useEffect(() => {
        if (!createProjectPending) {
            if (createProjectError) {
                dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_CREATE_PROJECT_STORE"))
                return;
            }
            if (createProjectRes) {
                if (createProjectRes?.code === 200) {
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_CREATE_PROJECT_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_ALL_PROJECT_LIST_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_PROJECT_STORE"))
                    toggleCreateUpdateProjectModal()
                    getAllProjects()
                }
            }
        }
    }, [
        createProjectRes,
        createProjectError,
        createProjectPending
    ]);

    useEffect(() => {
        if (!updateProjectPending) {
            if (updateProjectError) {
                dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_UPDATE_PROJECT_STORE"))
                return;
            }
            if (updateProjectRes) {
                if (updateProjectRes?.code === 200) {
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_UPDATE_PROJECT_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_CREATE_PROJECT_STORE"))
                    dispatch(departmentAction.handleClearDepartmentStore("HANDLE_CLEAR_GET_PROJECT_STORE"))
                    getAllProjects()
                    toggleCreateUpdateProjectModal()
                }
            }
        }
    }, [
        updateProjectRes,
        updateProjectError,
        updateProjectPending
    ]);


    useEffect(() => {
        getAllProjects()
    }, [currentPage])

    /* Get All Projects Data */
    const getAllProjects = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(departmentAction.getProjectRequest(params));
    }

    useEffect(() => {
        if (!addProjectPending) {
            if (getProjectError) {
                return;
            }
            if (getProjectRes) {
                if (getProjectRes?.code === 200) {
                    const getAllProjectData = getProjectRes?.data
                    setAllProjects(getAllProjectData)

                }
            }
        }
    }, [
        getProjectRes,
        getProjectError,
        addProjectPending
    ]);


    /*  Delete Project   */
    const handleProjectDeleteModal = (id) => {
        if (id) {
            setDeleteProjectId(id)
        }
        setIsShowDeleteProjectModal(!isShowDeleteProjectModal)
    }

    const handleDeleteProject = (type) => {
        if (type) {
            const params = {
                _id: deleteProjectId
            }

            // dispatch(departmentAction?.deleteProjectRequest(params))

        } else {
            handleProjectDeleteModal()
        }

    }

    useEffect(() => {
        if (!deleteProjectPending) {
            if (deleteProjectError) {
                // dispatch(departmentAction.handleClearProjectStore("HANDLE_CLEAR_DELETE_FUNCTION_STORE"))
                return;
            }
            if (deleteProjectRes) {
                if (deleteProjectRes?.code === 200) {
                    handleProjectDeleteModal()
                    if (currentPage > 1 && allProjects?.data?.length === 1) {
                        setCurrentPage((pre => pre -= 1))
                    } else {
                        getAllProjects()
                    }
                    // dispatch(departmentAction.handleClearProjectStore("HANDLE_CLEAR_DELETE_FUNCTION_STORE"))

                }
            }
        }
    }, [
        deleteProjectRes,
        deleteProjectError,
        deleteProjectPending
    ]);

    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        created_at: item => dateFormate(item.created_at),
        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                            onClick={() => toggleCreateUpdateProjectModal(item)}
                        >
                            <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                    </li>
                    {item?.emp_count === 0 && <li>
                        <span className="dropdown-item"
                            onClick={() => handleProjectDeleteModal(item._id)}
                        >
                            <Icon className={"bi bi-trash me-1"} />Delete</span>
                    </li>}
                </ul>
            </div>

        )
    }), [toggleCreateUpdateProjectModal]);


    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'department_name', label: 'Departments' },
        { key: 'name', label: 'Project Name' },
        { key: 'short_name', label: 'Short Name' },
        { key: 'emp_count', label: 'Total Employees' },
        { key: 'is_active', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];

    return (
        <>
            <div className='cardBox myRequest mb-3 employeeHistory'>
                <div className="d-flex align-items-center justify-content-between gap-3">
                    <div className="cardHead d-flex">
                        <img src="images/myrequest-icon.svg" alt="" width={35} />
                        <h4 className="ms-2 ">Projects</h4>
                    </div>
                    <div>
                        <CustomButton
                            classNames={"btn btn-primary gradientBg"}
                            handleClick={() => toggleCreateUpdateProjectModal()}
                            text={t("btns.createNewProject")}
                            icon={<Icon className={"fa fa-plus"} />}
                        />

                    </div>
                </div>
                <CustomTable
                    data={allProjects?.data}
                    fields={fields}
                    isLoading={addProjectPending}
                    pagination={true}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    totalPages={allProjects?.totalPages}
                    isHeader={false}
                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={scopedSlots}
                />
            </div>

            <CreateUpdateProject
                openModel={isOpenCreateUpdateProject}
                handleOpenClose={toggleCreateUpdateProjectModal}
                handleCreateUpdateProject={handleCreateUpdateProject}
                selectedProjectData={selectedProjectData}
                allDepartments={allDepartments}
                allEmployeeList={allEmployeeList}
                getSubDepartmentByDepartment={getSubDepartmentByDepartment}
                subDepartmentByDepartmentData={subDepartmentByDepartmentData}
            />
            {isShowDeleteProjectModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteProject}
                    isShow={isShowDeleteProjectModal}
                    desc={t("alerts.deletePopup.desc").replace('$1', "Project")}
                    btnRightLoading={deleteProjectPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}

                />)
            }
        </>
    )
}

export default Projects
