import React, { useEffect, useMemo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { departmentAction } from '../../../../core/resource/actions'
import CreateUpdateDepartment from './CreateUpdateDepartment'
import { pagination } from '../../../../utils/Constants.config'
import { CustomButton } from '../../../globalComponents'
import ApproveRejectModal from '../../../modal/ApproveRejectModal'
import DepartmentTable from "./DepartmentTable"
import { useCustomTranslation } from '../../../../translation'

const Department = () => {
  const { t } = useCustomTranslation()
  const dispatch = useDispatch();
  const {
    getAllDepartmentRes,
    getAllDepartmentError,
    getAllDepartmentPending,

    deleteDepartmentPending,
    deleteDepartmentRes,
    deleteDepartmentError,

    addDepartmentPending,
    addDepartmentRes,
    addDepartmentError,

    updateDepartmentPending,
    updateDepartmentRes,
    updateDepartmentError
  } = useSelector(state => state.department);

  const [departmentList, setDepartmentList] = useState('')
  const [selectedDepartmentData, setSelectedDepartmentData] = useState('')
  const [openCreateUpdateDepModal, setOpenCreateUpdateDepModal] = useState(false);
  const [deleteDepId, setDeleteDepId] = useState()
  const [isShowDeleteModal, setIsShowDeleteModal] = useState(false)
  const [currentPage, setCurrentPage] = useState(1);


  /* Create Update Department */
  const toggleDepartmentModal = (data) => {
    if (!data) {
      setSelectedDepartmentData('')
    } else {
      setSelectedDepartmentData(data)
    }
    setOpenCreateUpdateDepModal(!openCreateUpdateDepModal)
  }

  const handleCreateUpdateDepartment = (params) => {
    if (selectedDepartmentData) {
      params['_id'] = selectedDepartmentData?._id
      dispatch(departmentAction.updateDepartmentRequest(params));
    }
    else {
      dispatch(departmentAction.addDepartmentRequest(params));
    }
  }

  useEffect(() => {
    if (!addDepartmentPending) {
      if (addDepartmentError) {
        dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_ADD_DEPARTMENT_STORE'))
        return;
      }
      if (addDepartmentRes) {
        if (addDepartmentRes?.code == 200) {
          dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_ADD_DEPARTMENT_STORE'))
          dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_GET_DEPARTMENT_LIST_STORE'))
          toggleDepartmentModal()
          getAllDepartments();

        }
      }
    }
  }, [
    addDepartmentPending,
    addDepartmentRes,
    addDepartmentError,
  ]);

  useEffect(() => {
    if (!updateDepartmentPending) {
      if (updateDepartmentError) {
        dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_UPDATE_DEPARTMENT_STORE'))
        return;
      }
      if (updateDepartmentRes) {
        if (updateDepartmentRes?.code == 200) {
          dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_UPDATE_DEPARTMENT_STORE'))
          dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_GET_DEPARTMENT_LIST_STORE'))
          getAllDepartments();
          toggleDepartmentModal();
        }
      }
    }
  }, [
    updateDepartmentPending,
    updateDepartmentRes,
    updateDepartmentError
  ]);

  /* Get All Departments */
  useEffect(() => {
    getAllDepartments()
  }, [currentPage])

  const getAllDepartments = () => {
    const params = {
      page: currentPage,
      limit: pagination.limit,
    }
    dispatch(departmentAction.getAllDepartmentRequest(params));
  }

  useEffect(() => {
    if (!getAllDepartmentPending) {
      if (getAllDepartmentError) {
        return;
      }
      if (getAllDepartmentRes) {
        if (getAllDepartmentRes?.code === 200) {
          setDepartmentList(getAllDepartmentRes?.data)
        }
      }
    }
  }, [
    getAllDepartmentRes,
    getAllDepartmentError,
    getAllDepartmentPending
  ]);

  /* Delete Department */
  const onDeleteCancelDepartment = (type) => {
    if (type) {
      const params = {
        _id: deleteDepId
      }
      dispatch(departmentAction?.deleteDepartmentRequest(params))
    } else {
      toggleDeleteModal()
    }
  }
  const toggleDeleteModal = (id) => {
    if (id) {
      setDeleteDepId(id)
    }
    setIsShowDeleteModal(!isShowDeleteModal)
  }

  useEffect(() => {
    if (!deleteDepartmentPending) {
      if (deleteDepartmentError) {
        dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_DELETE_DEPARTMENT_STORE'))
        return;
      }
      if (deleteDepartmentRes) {
        if (deleteDepartmentRes?.code == 200) {
          dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_DELETE_DEPARTMENT_STORE'))
          dispatch(departmentAction.handleClearDepartmentStore('HANDLE_CLEAR_GET_DEPARTMENT_LIST_STORE'))
          toggleDeleteModal()
          if (currentPage > 1 && departmentList?.data?.length === 1) {
            setCurrentPage((pre => pre -= 1))
          } else {
            getAllDepartments()
          }
        }
      }
    }
  }, [
    deleteDepartmentRes,
    deleteDepartmentError,
    deleteDepartmentPending
  ]);

  return (
    <div>
      <div className="d-flex  justify-content-end ">
        <CustomButton
          classNames={"btn btn-primary gradientBg"}
          handleClick={() => toggleDepartmentModal()}
          text={t("btns.createDepartment")}
        />

      </div>
      <DepartmentTable
        isLoading={getAllDepartmentPending}
        toggleDepartmentModal={toggleDepartmentModal}
        setSelectedDepartmentData={setSelectedDepartmentData}
        toggleDeleteModal={toggleDeleteModal}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        departmentList={departmentList}
      />

      {openCreateUpdateDepModal && <CreateUpdateDepartment
        getAllDepartments={getAllDepartments}
        openCreateUpdateDepModal={openCreateUpdateDepModal}
        toggleDepartmentModal={toggleDepartmentModal}
        selectedDepartmentData={selectedDepartmentData}
        handleCreateUpdateDepartment={handleCreateUpdateDepartment}
      />}

      {isShowDeleteModal && (
        <ApproveRejectModal
          onApproveReject={onDeleteCancelDepartment}
          isShow={isShowDeleteModal}
          desc={t("alerts.deletePopup.desc").replace('$1', "Department")}
          btnRightLoading={deleteDepartmentPending}
          btnTextRight={t("btns.delete")}
          btnTextLeft={t("btns.cancel")}
        />)
      }
    </div>
  )
}

export default Department
